.page-template-page-resource {

    .pdf-container {
        position: relative;

        &.expanded {
            height: 900px;
        }

        .iframe-container {
            width: 100%;
            box-sizing: border-box;
            display: flex;
            flex: 0 1 auto;
            flex-direction: row;
            flex-wrap: wrap;
            margin: 2rem -1rem;
        }
    }

    .resource-pdf {
        height: 0;
        opacity: 0;
        position: absolute;
        top: 0;
        right: 0;
        width: 100%;
        transition: .2s ease all;

        &.opened {
            opacity: 1;
            height: 100%;
        }

        iframe {
            height: 800px;
            position: absolute;
            top: 0;
        }
    
    }

    .video-container {

    }
}